import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Login from './views/Login.vue'


Vue.use(Router)


export default new Router({
  scrollBehavior(to, from, savedPosition) {
    //scrollBehavior  to  from  savedPosition  popstate  ( / ) 
    if (savedPosition) {
      return savedPosition
    } else {
      return { x: 0, y: 0 }
    }
  },
 // mode:"history",
  routes: [
    {
      path: '/',
      name: 'home',
     // component: () => import('./views/Home.vue'),
      component: Home,
      meta: {
        title: '',
      },
    },
    {
      path: '/login',
      name: 'login',
      component:Login,
      meta: {
        title: '',
      },
    },
    {
      path: '/register',
      name: 'register',
      // route level code-splitting
      // this generates a separate chunk (test.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "register" */ './views/Register.vue'),
      meta: {
        title: '',
      },
    },
    {
      path: '/rank',
      name: 'rank',
	  component: () => import('./views/Rank.vue'),
	  meta: {
        title: '',
        auth:1,
      },
	},
	{
		path: '/rank/:id',
		name: 'rankSearch',
		component: () => import('./views/Rank.vue'),
		meta: {
			title: '',
			auth:1,
		},
	},
    {
      path: '/catg',
      name: 'catg',
	  component: () => import('./views/Catg.vue'),
	  meta: {
        title: '',
        auth:1,
      },
    },
    {
      path: '/detail/:id',
      name: 'detail',
	  component: () => import('./views/Detail.vue'),
	  meta: {
        title: '',
        auth:1,
      },
    },
    {
      path: '/detailzbxg/:id',
      name: 'detailzbxg',
	  component: () => import('./views/DetailZbxg.vue'),
	  meta: {
        title: '',
        auth:1,
      },

    },
    {
      path: '/detailzbpk/:id/:toid',
      name: 'detailzbpk',
      component: () => import('./views/DetailZbpk.vue'),
	  meta: {
        title: 'pk',
        auth:1,
      },
    },
    {
      path:'/forgetPassword',
      name:"forgetPassword",
	  component: () => import('./views/User/ForgetPassword.vue'),
	  meta: {
        title: '',
      },
    },
    {
      path:'/userCenter',
      name:"userCenter",
	  component: () => import( './views/User/UserCenter.vue'),
	  meta: {
        title: '',
        auth:1,
      },
      children: [
        {
          path: 'info',
          name:"userInfo",
		  component: () => import('./views/User/UserInfo.vue'),
		  meta: {
			title: '',
			auth:1,
		  },
        },
        {
          path: 'fav',
          name:"userFav",
		  component: () => import('./views/User/UserFav.vue'),
		  meta: {
			title: '',
			auth:1,
		  },
        },
          {
          path: 'password',
          name:"userPassword",
		  component: () => import('./views/User/UserPassword.vue'),
		  meta: {
			title: '',
			auth:1,
		  },
        },
          {
          path: 'email',
          name:"userEmail",
		  component: () => import('./views/User/UserEmail.vue'),
		  meta: {
			title: '',
			auth:1,
		  },
        },
          {
          path: 'phone',
          name:"userPhone",
		  component: () => import('./views/User/UserPhone.vue'),
		  meta: {
			title: '',
			auth:1,
		  },
        }
      ]
    }
  ]
})
